.author-list .author-item-cnt {
  padding-left: 5px;
  padding-right: 5px;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  line-height: 100px;
  color: #fff;
}


/**/

</style>

<script type="ecmascript-6">
import Swiper from '../../../static/lib/swiper.min.js'
export default {
  name: 'manga',
  mounted () {
    this.lunbo()
  },
  methods: {
    lunbo () {
      this.Swiper = new Swiper('.swiper-container', {
        loop: true,
        autoplay: 2000,
        pagination: '.swiper-pagination'
      })
    }
  }
}
</script>



